(function () { // EFI (Expresión de función invocada) - IIFE (Immediately Invoked Function Expression) - Función anónima autoejecutable - Closure - Módulo
    const tagsInput = document.querySelector('#tags_input');

    if (tagsInput) {
        tagsDiv = document.querySelector('#tags');
        tagsInputHidden = document.querySelector('[name="tags"]');
        let tags = [];

        //Recuperar del input hidden los tags que ya existen

        if(tagsInputHidden.value !== '') {
            tags = tagsInputHidden.value.split(',');
            mostrarTags();
        }

        //Escuchar los cambios en el input 
        tagsInput.addEventListener('keypress', guardarTag);

        function guardarTag(e) {
            if (e.keyCode === 44) {
                if (e.target.value.trim() === '' || e.target.value < 1) {
                    return;
                }
                e.preventDefault();
                tags = [...tags, e.target.value.trim()];
                tagsInput.value = '';

                mostrarTags();
            }
        }

        function mostrarTags() {
            tagsDiv.textContent = '';
            tags.forEach(tag => {
                const etiqueta = document.createElement('LI');
                etiqueta.classList.add('formulario__tag');
                etiqueta.textContent = tag;
                etiqueta.ondblclick = eliminarTag;
                tagsDiv.appendChild(etiqueta);
            })
            actualizarInputHidden();
        }

        function eliminarTag(e) {
            e.target.remove();
            tags = tags.filter(tag => tag !== e.target.textContent);
            actualizarInputHidden();
        }

        function actualizarInputHidden() {
            tagsInputHidden.value = tags.toString();
        }
    }
})();